'use strict';

document.addEventListener('DOMContentLoaded', function() {
	let site = document.querySelector('.site');
	const buttonToggleTheme = document.querySelector('.jsToggleTheme');
    
	function handleClick(){
		config.darkTheme = config.darkTheme === 'true' ? 'false' : 'true';
		toggleTheme();
		localStorage.setItem('app_config', JSON.stringify(config));
	}
    
	function toggleTheme(){
		if(config.darkTheme === 'true'){
			site.classList.add('dark-theme');
			buttonToggleTheme.classList.add('btn--on');
		}else {
			site.classList.remove('dark-theme');
			buttonToggleTheme.classList.remove('btn--on');
		}
	}

	const ls = localStorage.getItem('app_config');
	const config = ls ? JSON.parse(ls) : {
		menuClosed : false,
		fontSize : false,
		darkTheme : false
	};
    
	buttonToggleTheme.addEventListener('click',handleClick);
	toggleTheme();
});